//=============================================
//=              External Mixins              =
//============================================/

@import "bootstrap/scss/mixins";


// Specified breakpoint
@mixin media($name) {
  @include media-breakpoint-only($name) {
    @content;
  };
}

// Larger than specified breakpoint
@mixin media-up($name) {
  @include media-breakpoint-up($name) {
    @content;
  };
}

// smaller than specified breakpoint
@mixin media-down($name) {
  @include media-breakpoint-down($name) {
    @content;
  };
}

// /******************************************
// *           Placeholder
// *******************************************/
@mixin placeholder() {
  &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    @content;
  }
  &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    @content;
  }
  &::-moz-placeholder { /* Mozilla Firefox 19+ */
    @content;
  }
  &:-ms-input-placeholder { /* Internet Explorer 10-11 */
    @content;
  }
}


@mixin debug-map($map) {
  @at-root {
    @debug-map {
      __toString__: inspect($map);
      __length__: length($map);
      __depth__: depth($map);
      __keys__: map-keys($map);
      __properties__ {
        @each $key, $value in $map {
          #{'(' + type-of($value) + ') ' + $key}: inspect($value);
        }
      }
    }
  }
}

.Sidebar {

  $SidebarPaddingHorizontal: 20px;

  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  z-index: 1;
  color: var(--SidebarColorText);
  background-color: var(--SidebarColorBg);
  box-shadow: var(--SidebarShadow);
  transition: background-color .3s ease;


  @include media-down(md) {
    box-shadow: none;

    .-sidebar-open-mobile & {
      box-shadow: var(--SidebarShadow);
    }
  }

  @include media-up(lg) {
    .-sidebar-closed-desktop & {
      box-shadow: none;
    }
  }


  // Sidebar background image
  &:before {
    position: absolute;
    width: 100%;
    height:100%;
    left:0;
    top:0;
    display: block;
    content: ' ';
    background-position: center;
    background-repeat: repeat;
    background-size: cover;
    background-image: var(--SidebarBgImage);
    opacity: var(--SidebarBgImageOpacity);
    z-index: -1;
  }

  /*----------  Sidebar Header  ----------*/

  .SidebarHeader {
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    padding: 0 $SidebarPaddingHorizontal;
    flex-shrink: 0;
    height: var(--AppHeaderHeight);
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);
    // font-family: 'Montserrat', sans-serif;

    .-rtl & {
      flex-direction: row-reverse;
    }
  }

  .Logo {
    width: 38px;
    height: 38px;
    display: block;

    // https://stackoverflow.com/questions/6088409/svg-drop-shadow-using-css3
    // https://github.com/Schepp/CSS-Filters-Polyfill
    -webkit-filter: drop-shadow( 0 0 3px rgba(0, 0, 0, 0.3) );
            filter: drop-shadow( 0 0 3px rgba(0, 0, 0, 0.3) );

    .path {
      transition: all .3s ease;
    }

    .outline {
      fill: var(--colorPrimary);
    }

    .letter {
      fill: #fff;
      opacity: .75;
    }

    &:hover {
      .outline {

      }

      .letter {
        opacity: 1;
      }
    }
  }

  .LogoLink {
    margin-left: 5px;
    margin-right: 12px;

    .-rtl & {
      margin-left: 12px;
      margin-right: 5px;
    }
  }

  .Title {
    margin: 0;
    font-size: 1rem;
    // font-weight: bold;
    color: #fff;
    position: relative;
    white-space: nowrap;
    // top: -1px;
    text-shadow: 0 0 3px rgba(0, 0, 0, 0.4);

    .Link {
      text-decoration: none;
      color: inherit;
      display: flex;
    }

    .Version {
      color: $colorText-muted;
      margin-left: 5px;
    }

    .Platform {
      position: absolute;
      display: block;
      text-decoration: none;
      top: 100%;
      margin-top: 2px;
      right: 0;
      font-size: 9px;
      font-weight: bold;
      color: $colorText-muted;
      transition: color .3s ease;
      text-shadow: 0 0 2px rgba(0, 0, 0, 0.4);

      &:hover {
        color: #fff;
      }
    }

  }

  /*----------  Sidebar Content  ----------*/

  .SidebarContent {
    flex: 1;
    overflow-x: visible;
    overflow-y: auto;
    border-bottom: 1px solid rgba(255, 255, 255, 0.15);

    &::-webkit-scrollbar {
      width: 14px;
      height: 18px;
    }

    &::-webkit-scrollbar-thumb {
      height: 6px;
      border: 4px solid rgba(0, 0, 0, 0);
      background-clip: padding-box;
      -webkit-border-radius: 7px;
      -webkit-box-shadow: none;
      background-color: rgba(0, 0, 0, 0.1);
    }

    &:hover {
      &::-webkit-scrollbar-thumb {
        // background-color: rgba(0, 0, 0, 0.35);
        background-color: var(--colorPrimary);
        -webkit-box-shadow: inset -1px -1px 0px rgba(0, 0, 0, 0.05), inset 1px 1px 0px rgba(0, 0, 0, 0.05);
      }
    }
  }


  .SidebarNav {

    $font-size: 15px;

    $icon-font-size: 20px;
    $icon-font-size-nested: 15px;

    $icon-box-width: 26px;
    $icon-box-width-nested: 22px;

    $icon-spacing: 5px;

    $badge-font-size: 25px;
    $badge-font-nested: 25px;

    $item-height: 44px;
    $item-height-nested: 36px;
    $item-padding-base: $SidebarPaddingHorizontal + $icon-box-width + $icon-spacing;

    $sumbenu-padding: 6px;
    $submenu-levels: 4;


    font-size: $font-size;
    padding-bottom: 15px;

    .Icon {
      position: absolute;
      font-size: $icon-font-size;
      width: $icon-box-width;
      height: $item-height;
      display: flex;
      align-items: center;
      justify-content: center;
      left: $SidebarPaddingHorizontal;
      top: 0;

      .-rtl & {
        left: auto;
        right: $SidebarPaddingHorizontal;
      }
    }

    .Divider {
      display: block;
      // width: 100%;
      height: 1px;
      margin: 3px $SidebarPaddingHorizontal;
      background-color: rgba(255, 255, 255, 0.13);
    }

    // General styles for items
    a {
      position: relative;
      display: flex;
      width: 100%;
      height: $item-height;
      align-items: center;
      justify-content: flex-start;
      text-decoration: none;
      transition: color .3s ease, background .3s ease;
      color: var(--SidebarNavColorText);
      cursor: pointer;
      padding: 0 $SidebarPaddingHorizontal 0 $item-padding-base;
      font-weight: normal;

      &.-active,
      &.-open {
        color: var(--SidebarNavColorText-active);
      }

      &:hover {
        color: var(--SidebarNavColorText-active);
      }

      &.-accented {
        & > .Icon {
          color: var(--colorPrimary);
        }
      }

      .-rtl & {
        justify-content: flex-end;
        padding: 0 $item-padding-base 0 $SidebarPaddingHorizontal;
      }
    }

    // Nested items
    .NavGroup nav {
      overflow-x: visible;
      overflow-y: hidden;
      height: 0;

      a {
        font-size: 0.9em;
        height: $item-height-nested;
      }

      // .icon {
      //   font-size: $icon-font-size-nested;
      //   height: $item-height-nested;
      //   width: $icon-box-width-nested;
      // }
    }

    // Nested items padding
    $sel: '';

    @for $i from 1 through $submenu-levels {

      $padding-base:  $icon-box-width + $SidebarPaddingHorizontal;
      $sel: if($i == 1, 'nav', selector-nest($sel, 'nav')); //selector-nest($sel, "nav")) !global;;

      // $padding-base +
      // $sel: if($i == 1, "nav", selector-nest($sel, "nav")) !global;

      // #{$sel} {
      //     padding-left:  10px * $i;
      // }

      #{$sel} a {
        padding-left: $item-padding-base + $sumbenu-padding * $i;

        .Icon {
          left: $SidebarPaddingHorizontal + $sumbenu-padding * $i;
        }

        .-rtl & {
          padding-left: 0;
          padding-right: $item-padding-base + $sumbenu-padding * $i;

          .Icon {
            left: auto;
            right: $SidebarPaddingHorizontal + $sumbenu-padding * $i;
          }
        }
      }
    }
  }


  /*----------  Sidebar Footer  ----------*/

  .SidebarFooter {

    $height: $AppFooterHeight;

    height: $height;
    flex-shrink: 0;
    display: flex;
    width: 100%;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.09);
    position: relative;

    .SidebarFooterNav {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      display: flex;
      flex-direction: row;

      a {
        font-weight: normal;
        display: flex;
        align-items: center;
        text-decoration: none;
        transition: background-color .3s ease, color .3s ease;
        color: var(--SidebarNavColorText);


        &:hover {
          color: var(--SidebarNavColorText-active);
          background-color: rgba(255, 255, 255, 0.05);
        }
      }

      .-rtl & {
        flex-direction: row-reverse;
      }
    }

    .LinkCustomize {
      flex: 1;

      justify-content: flex-start;
      padding: 0 $SidebarPaddingHorizontal 0 $SidebarPaddingHorizontal + 31px;
      border-right: 1px solid rgba(255, 255, 255, 0.1);
      flex-grow: 3.5;

      .Icon {
        position: absolute;
        font-size: 20px;
        width: 26px;
        height: $height;
        display: flex;
        align-items: center;
        justify-content: center;
        left: $SidebarPaddingHorizontal;
        top: 0;
      }


      .-rtl & {
        border-right: none;
        border-left: 1px solid rgba(255, 255, 255, 0.1);

        .Icon {
          left: auto;
          right: $SidebarPaddingHorizontal;
        }
      }
    }

    .LinkCollapse {
      justify-content: center;
      flex-grow: 1;
    }
  }


}



