//=============================================
//=              External Mixins              =
//============================================/

@import "bootstrap/scss/mixins";


// Specified breakpoint
@mixin media($name) {
  @include media-breakpoint-only($name) {
    @content;
  };
}

// Larger than specified breakpoint
@mixin media-up($name) {
  @include media-breakpoint-up($name) {
    @content;
  };
}

// smaller than specified breakpoint
@mixin media-down($name) {
  @include media-breakpoint-down($name) {
    @content;
  };
}

// /******************************************
// *           Placeholder
// *******************************************/
@mixin placeholder() {
  &::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    @content;
  }
  &:-moz-placeholder { /* Mozilla Firefox 4 to 18 */
    @content;
  }
  &::-moz-placeholder { /* Mozilla Firefox 19+ */
    @content;
  }
  &:-ms-input-placeholder { /* Internet Explorer 10-11 */
    @content;
  }
}


@mixin debug-map($map) {
  @at-root {
    @debug-map {
      __toString__: inspect($map);
      __length__: length($map);
      __depth__: depth($map);
      __keys__: map-keys($map);
      __properties__ {
        @each $key, $value in $map {
          #{'(' + type-of($value) + ') ' + $key}: inspect($value);
        }
      }
    }
  }
}

// Font Awesome Icons

$fa-font-path: '~font-awesome/fonts';

@import 'font-awesome/scss/font-awesome.scss';

// Pe 7s icons
$pe-7s-font-path: '~pixeden-stroke-7-icon/pe-icon-7-stroke/fonts';

@import 'pixeden-stroke-7-icon/pe-icon-7-stroke/scss/pe-icon-7-stroke.scss';

.Icon {
  .-x1point5 {
    font-size: 1.5em;
  }

  &.-x2 {
    font-size: 2em;
  }

  &.-x3 {
    font-size: 3em;
  }

  &.-x4 {
    font-size: 4em;
  }

  &.-x5 {
    font-size: 5em;
  }

  &.-x6 {
    font-size: 6em;
  }
}



